@import "../../scss/configurations";

.section-experience {
    padding: 100px 0;

    &__container {
        @include FlexBox;
        gap: 30px;
        margin: 75px 0;
    }

    &__card {
        
        min-height: 568px;
        width: 35%;
        min-width: 200px;
        @include FlexBox(column);

        &-div {
            @include FlexBox;
        }

        &-img {
        width: 18px;
        margin-right: 10px;
        align-self: flex-start;
        }

        &-text_container {
            padding: 0 21px;
        }

        &-years {
            color: $violet-100;
            font-size: 0.875rem;
            margin-bottom:  7px;
        }

        &-title {
            font-weight: 800;
            font-size: 18px;
            margin: 7px 0;
            
        }

        &-list {
            list-style: circle;
            padding: 0 17px;
        
        }

        &-p {
            color: $gray;
        }
    }
}

.vl {
    border-left: 2px solid $red-100;
    height: 235px;
    margin-left: 7px;
    margin-top: 0;
}